import React from "react";
import PermissionProvider from "src/utils/provider/Permission";
import AppThemeProvider from "src/utils/provider/AppTheme";
import LocaleProvider from "./utils/i18n";
import SessionProvider from "./utils/provider/Session";
import CrashlyticsProvider from "./utils/provider/Crashlytics";
import Main from "./main";
import store from "./redux/store";
import firebase from "firebase/app";
import { Provider } from "react-redux";

type Props = {
  firebaseApp: firebase.app.App;
};

const App: React.FC<Props> = ({ firebaseApp }) => {
  return (
    <CrashlyticsProvider firebaseApp={firebaseApp}>
      <PermissionProvider>
        <AppThemeProvider>
          <LocaleProvider>
            <SessionProvider firebaseApp={firebaseApp}>
              <Provider store={store}>
                <Main />
              </Provider>
            </SessionProvider>
          </LocaleProvider>
        </AppThemeProvider>
      </PermissionProvider>
    </CrashlyticsProvider>
  );
};

export default App;
