import React, { ReactNode } from "react";
import { ClientConfig } from "../../../db/client/models/ClientConfig";
import {
  getByKey as getClientConfigByKey,
  save as saveClientConfig,
} from "../../../db/client/repositories/ClientConfigRepository";
import { cdb } from "../../../db/ClientDB";
import darkTheme from "./templates/dark";
import defaultTheme from "./templates/default";
import { APP_THEME_KEY } from "../../../constants/keys";
import { Theme, MuiThemeProvider } from "@material-ui/core";

const DEFAULT_THEME: ThemeMode = "default";
const DARK_THEME: ThemeMode = "dark";

export type ThemeMode = "default" | "dark";

export type ThemeContext = {
  mode: ThemeMode;
  theme?: Theme;
  changeTheme?: (mode: ThemeMode) => Promise<boolean>;
  themeList?: Array<string>;
};

const defaultContext: ThemeContext = {
  mode: DEFAULT_THEME,
  theme: defaultTheme,
  changeTheme: undefined,
  themeList: [DEFAULT_THEME, DARK_THEME],
};

const Context = React.createContext<ThemeContext>(defaultContext);

const { Consumer, Provider } = Context;

export { Consumer, Context };

type Props = {
  children: ReactNode;
};

type State = {
  resolved: boolean;
  mode: ThemeMode;
  theme: Theme;
  error?: any;
};

class ThemeProvider extends React.Component<Props, State> {
  readonly state: State = {
    resolved: false,
    mode: DEFAULT_THEME,
    theme: defaultTheme,
  };

  changeTheme = async (mode: ThemeMode): Promise<boolean> => {
    try {
      const clientConfigData = await saveClientConfig(
        new ClientConfig(APP_THEME_KEY, mode),
        cdb
      );
      console.log(clientConfigData);
      let theme = defaultTheme;
      if (mode === DARK_THEME) {
        theme = darkTheme;
      }
      this.setState({
        mode,
        theme,
      });
      return true;
    } catch (error) {
      this.setState({
        error,
      });
      return false;
    }
  };

  getAppTheme = async (): Promise<ThemeMode> => {
    const clientConfig: ClientConfig | undefined = await getClientConfigByKey(
      APP_THEME_KEY,
      cdb
    );
    if (clientConfig) {
      return clientConfig.value as ThemeMode;
    }
    return DEFAULT_THEME;
  };

  componentDidMount() {
    this.getAppTheme()
      .then((mode: ThemeMode) => {
        let theme = defaultTheme;
        if (mode === DARK_THEME) {
          theme = darkTheme;
        }
        this.setState(
          {
            mode,
            theme,
          },
          () => {
            this.setState({
              resolved: true,
            });
          }
        );
      })
      .catch((error) => {
        this.setState({ error }, () => {
          this.setState({ resolved: true });
        });
      });
  }

  render() {
    const { resolved, theme } = this.state;
    return (
      resolved && (
        <Provider
          value={{
            ...this.state,
            changeTheme: this.changeTheme,
          }}
        >
          <MuiThemeProvider theme={theme}>
            {this.props.children}
          </MuiThemeProvider>
        </Provider>
      )
    );
  }
}

export default ThemeProvider;
