import React, { ReactElement } from "react";
import { Route, RouteComponentProps } from "react-router-dom";
import { SessionConsumer } from "../provider/Session";

type Props = {
  children?: (props: RouteComponentProps<any>) => ReactElement;
  component: (props: RouteComponentProps<any>) => ReactElement;
  path?: string;
  exact?: boolean;
};

const AppRoutes: React.FC<Props> = ({ children, component, path, exact }) => {
  const ChildComponent = children || component;
  return (
    <SessionConsumer>
      {({ loading, isAuthenticated }) => {
        return (
          !loading && (
            <Route
              path={path}
              exact={exact}
              render={(props: any) => {
                return <ChildComponent {...props} />;
              }}
            />
          )
        );
      }}
    </SessionConsumer>
  );
};

export default AppRoutes;
