import React, { ReactNode } from "react";
import { v4 as uuidV4 } from "uuid";
import firebase from "firebase/app";
import "firebase/auth";
import { APP_CLIENT_ID, APP_SESSION_ID } from "src/constants/keys";
import { CrashlyticsConsumer } from "../Crashlytics";

export type SessionContext = {
  loading: boolean;
  error?: any;
  user: User | null;
  isAuthenticated: boolean;
  firebaseAuth?: firebase.auth.Auth;
};

const defaultContext: SessionContext = {
  loading: true,
  user: null,
  isAuthenticated: false,
};

const Context = React.createContext<SessionContext>(defaultContext);

const { Consumer, Provider } = Context;

export { Consumer, Context };

type Props = {
  children: ReactNode;
  firebaseApp: firebase.app.App;
};

type User = {
  name: string | null;
  profile: string | null;
  email: string | null;
  isVerified: boolean;
};

type State = {
  resolved: boolean;
  loading: boolean;
  error?: any;
  isAuthenticated: boolean;
  user: User | null;
  sessionId?: string;
  clientId?: string;
};

class SessionProvider extends React.Component<Props, State> {
  private firebaseAuth: firebase.auth.Auth;

  constructor(props: Props) {
    super(props);
    this.state = {
      resolved: false,
      loading: true,
      error: undefined,
      isAuthenticated: false,
      user: null,
    };
    const { firebaseApp } = props;
    this.firebaseAuth = firebaseApp.auth();
  }

  componentDidMount() {
    const user = this.firebaseAuth.currentUser;
    let sessionId: string | null = sessionStorage.getItem(APP_SESSION_ID);
    let clientId: string | null = localStorage.getItem(APP_CLIENT_ID);
    if (!sessionId) {
      sessionId = uuidV4();
      sessionStorage.setItem(APP_SESSION_ID, sessionId);
    }
    if (!clientId) {
      clientId = uuidV4();
      localStorage.setItem(APP_CLIENT_ID, clientId);
    }
    this.setState({
      resolved: true,
      loading: false,
      isAuthenticated: user ? true : true,
      user: user
        ? {
            name: user.displayName,
            profile: user.photoURL,
            email: user.email,
            isVerified: user.emailVerified,
          }
        : null,
      clientId,
      sessionId,
    });
  }

  render() {
    const {
      loading,
      error,
      clientId,
      sessionId,
      user,
      isAuthenticated,
    } = this.state;
    return (
      <CrashlyticsConsumer>
        {({ firebaseAnalytics }) => {
          if (firebaseAnalytics) {
            firebaseAnalytics.setUserProperties({
              app_client_id: clientId,
              app_session_id: sessionId,
            });
          }
          return (
            <Provider
              value={{
                loading,
                error,
                isAuthenticated,
                user,
                firebaseAuth: this.firebaseAuth,
              }}
            >
              {this.props.children}
            </Provider>
          );
        }}
      </CrashlyticsConsumer>
    );
  }
}

export default SessionProvider;
