import React, { Component, ReactNode } from "react";
import appLocales from "./utils";

import { I18nProvider } from "@lingui/react";
import { ClientConfig } from "src/db/client/models/ClientConfig";
import {
  getByKey as getClientConfigByKey,
  save as saveClientConfig,
} from "src/db/client/repositories/ClientConfigRepository";
import { cdb } from "src/db/ClientDB";
import { LOCALE_NAME_KEY } from "src/constants/keys";

import LocaleError from "../errors/LocaleError";

export type LocaleName = "hi" | "en" | "gu";

const LOCALE_HI: LocaleName = "hi";
const LOCALE_EN: LocaleName = "en";
const LOCALE_GU: LocaleName = "gu";

export { LOCALE_EN, LOCALE_GU, LOCALE_HI };

export type AppLocale = {
  id: number;
  name: string;
  locale: LocaleName;
  isSupported: boolean;
};

export type LocaleContext = {
  loading: boolean;
  error?: any;
  locale: LocaleName;
  changeLocale?: (localeName: LocaleName) => Promise<boolean>;
  localeList: Array<AppLocale>;
};

const defaultContext: LocaleContext = {
  loading: false,
  locale: LOCALE_EN,
  localeList: appLocales,
};

const Context = React.createContext<LocaleContext>(defaultContext);

const { Consumer, Provider } = Context;

export { Consumer, Context };

type Props = {
  children: ReactNode;
};

type State = {
  resolved: boolean;
  loading: boolean;
  locale: LocaleName;
  error?: any;
  catalogs?: any;
  appLocales: Array<AppLocale>;
};

class LocaleProvider extends Component<Props, State> {
  readonly state: State = {
    resolved: false,
    loading: true,
    locale: LOCALE_EN,
    error: null,
    catalogs: undefined,
    appLocales: appLocales,
  };

  changeLocale = async (localeName: LocaleName): Promise<boolean> => {
    try {
      this.setState({
        loading: true,
      });
      const catalog = await this.loadCatalog(localeName);
      await saveClientConfig(
        new ClientConfig(LOCALE_NAME_KEY, localeName),
        cdb
      );
      this.setState((state) => ({
        loading: false,
        catalogs: {
          ...state.catalogs,
          [localeName]: catalog,
        },
        locale: localeName,
      }));
      return true;
    } catch (error) {
      this.setState({
        loading: false,
        error,
      });
      return false;
    }
  };

  loadCatalog = async (localeName: LocaleName): Promise<any> => {
    const { appLocales } = this.state;
    if (
      appLocales.filter((appLocale: AppLocale) => {
        return appLocale.locale === localeName && appLocale.isSupported;
      }).length === 0
    ) {
      throw new LocaleError(
        "LocaleLoadError",
        "Locale is not loaded or not supported"
      );
    }
    const catalog = await import(`./locales/${localeName}/messages`);
    return catalog.default;
  };

  getLocale = async (): Promise<LocaleName> => {
    const clientConfig: ClientConfig | undefined = await getClientConfigByKey(
      LOCALE_NAME_KEY,
      cdb
    );
    if (clientConfig) {
      return clientConfig.value as LocaleName;
    }
    return LOCALE_EN;
  };

  init = async (): Promise<void> => {
    try {
      const localeName: LocaleName = await this.getLocale();
      const catalog = await this.loadCatalog(localeName);
      this.setState(
        (state) => ({
          loading: false,
          catalogs: {
            ...state.catalogs,
            [localeName]: catalog,
          },
          locale: localeName,
          error: null,
        }),
        () => {
          this.setState({
            resolved: true,
          });
        }
      );
    } catch (error) {
      this.setState({
        resolved: true,
        loading: false,
        error,
      });
    }
  };

  componentDidMount() {
    this.init();
  }
  render() {
    const {
      catalogs,
      locale,
      loading,
      error,
      resolved,
      appLocales,
    } = this.state;
    return (
      resolved && (
        <Provider
          value={{
            loading,
            error,
            locale,
            localeList: appLocales,
            changeLocale: this.changeLocale,
          }}
        >
          <I18nProvider language={locale} catalogs={catalogs}>
            {this.props.children}
          </I18nProvider>
        </Provider>
      )
    );
  }
}

export default LocaleProvider;
