import { ClientConfig, IClientConfig } from "../models/ClientConfig";
import { ClientDB } from "../../ClientDB";

export const save = async (
  clientConfig: ClientConfig,
  db: ClientDB
): Promise<any> => {
  return db.clientConfig.put(clientConfig);
};

export const getByKey = async (key: string, db: ClientDB): Promise<any> => {
  return db.clientConfig.get({ key });
};

export const deleteByKey = async (key: string, db: ClientDB): Promise<any> => {
  return db.clientConfig.where({ key }).delete();
};

export const isKeyExist = async (
  key: string,
  db: ClientDB
): Promise<IClientConfig | undefined> => {
  return db.clientConfig.get(key);
};
