import {
  Container,
  createStyles,
  LinearProgress,
  Theme,
  CssBaseline,
  withStyles,
  WithStyles,
} from "@material-ui/core";
import { Color } from "@material-ui/lab/Alert";
import React, { ReactNode } from "react";
import { Trans } from "@lingui/react";
import { appPermissions } from "./permissions";
import { cdb } from "src/db/ClientDB";

import SnackBarMessage from "src/components/SnackBarMessage";

export type AppPermission = {
  key: string;
  name: string;
  required: boolean;
  runtime: boolean;
  info: string;
  isAllowed: boolean;
};

export type AppPermissionContext = {
  online: boolean;
  permissions: AppPermission[];
  requestForPermission?: (key: string) => Promise<number>;
  isOnline?: () => boolean;
};

const defaultContext: AppPermissionContext = {
  online: true,
  permissions: [],
};

const Context = React.createContext<AppPermissionContext>(defaultContext);

const { Consumer, Provider } = Context;

export { Consumer, Context };

type Props = {
  children: ReactNode;
};

type SnackBarMessageData = {
  key: string;
  open: boolean;
  severity?: Color;
  message: string;
};

type State = {
  resolved: boolean;
  loading: boolean;
  error?: any;
  permissions: AppPermission[];
  online: boolean;
  snackBarMessageData?: SnackBarMessageData;
};

const styles = (theme: Theme) =>
  createStyles({
    container: {
      margin: 0,
      padding: 0,
      maxWidth: "100%",
    },
    loading: {
      height: "0.5rem",
      width: "100%",
    },
  });

class PermissionProvider extends React.Component<
  Props & WithStyles<typeof styles>,
  State
> {
  readonly state: State = {
    resolved: false,
    loading: true,
    error: null,
    online: true,
    permissions: [],
    snackBarMessageData: undefined,
  };

  handleNetworkEvent = (eventName: "online" | "offline") => (e: any) => {
    let snackBarMessageData: SnackBarMessageData = {
      open: true,
      message: "YouAreConnectedToInternet",
      severity: "success",
      key: `${new Date()}`,
    };
    if (eventName === "offline") {
      snackBarMessageData.message = "YouAreNotConnectedToInternet";
      snackBarMessageData.severity = "error";
    }
    this.setState({
      online: eventName === "online",
      snackBarMessageData,
    });
  };

  requestForPermission = async (permissionName: string): Promise<number> => {
    try {
      const requestPermission = this.state.permissions.filter(
        (data: AppPermission) =>
          data.key === permissionName &&
          ["camera", "microphone", "notification", "geolocation"].includes(
            permissionName
          )
      );
      if (requestPermission.length === 1) {
        const permissionState = await navigator.permissions.query({
          name: permissionName as PermissionName,
        });
        if (permissionState.state === "granted") {
          return 1;
        } else if (permissionState.state === "prompt") {
          return 2;
        } else {
          throw new Error(
            `Permission of ${requestPermission[0].name} is required!`
          );
        }
      } else {
        throw new Error(
          `Permission function didn't configured for ${permissionName}`
        );
      }
    } catch (error) {
      return 0;
    }
  };

  isOnline = (): boolean => {
    try {
      return navigator && navigator.onLine;
    } catch (error) {
      return false;
    }
  };

  isCookieEnabled = (): boolean => {
    try {
      return navigator && navigator.cookieEnabled;
    } catch (error) {
      return false;
    }
  };

  isDBOpen = async (): Promise<boolean> => {
    try {
      await cdb.open();
      return true;
    } catch (error) {
      return false;
    }
  };

  isAppStorageAllowed = async (): Promise<boolean> => {
    try {
      let localAnsSessionStorage = false;
      if (window.localStorage && window.sessionStorage) {
        localAnsSessionStorage = true;
      }
      const database = await this.isDBOpen();
      return localAnsSessionStorage && database;
    } catch (error) {
      return false;
    }
  };

  handleSnackBarMessageClose = (
    event?: React.SyntheticEvent,
    reason?: string
  ) => {
    if (reason === "clickaway") {
      return;
    }
    this.setState({
      snackBarMessageData: {
        ...this.state.snackBarMessageData,
        open: false,
        message: "",
        key: `${new Date()}`,
      },
    });
  };

  componentDidMount() {
    window.addEventListener("online", this.handleNetworkEvent("online"));
    window.addEventListener("offline", this.handleNetworkEvent("offline"));
    Promise.allSettled([
      this.isOnline(),
      this.isCookieEnabled(),
      this.isAppStorageAllowed(),
      this.requestForPermission("camera"),
      this.requestForPermission("microphone"),
      this.requestForPermission("notification"),
      this.requestForPermission("geolocation"),
    ])
      .then((permissionResponse) => {
        const isOnline: boolean =
          permissionResponse[0].status === "fulfilled" &&
          permissionResponse[0].value;
        const permissions = appPermissions.map(
          (permissionObj: AppPermission) => {
            if (permissionObj.key === "cookie") {
              permissionObj.isAllowed =
                permissionResponse[1].status === "fulfilled"
                  ? permissionResponse[1].value
                  : false;
            }
            if (permissionObj.key === "app-storage") {
              permissionObj.isAllowed =
                permissionResponse[2].status === "fulfilled" &&
                permissionResponse[2].value;
            }
            if (permissionObj.key === "location") {
              permissionObj.isAllowed =
                permissionResponse[3].status === "fulfilled" &&
                permissionResponse[3].value === 1;
            }
            if (permissionObj.key === "camera") {
              permissionObj.isAllowed =
                permissionResponse[4].status === "fulfilled" &&
                permissionResponse[4].value === 1;
            }
            if (permissionObj.key === "microphone") {
              permissionObj.isAllowed =
                permissionResponse[5].status === "fulfilled" &&
                permissionResponse[5].value === 1;
            }
            if (permissionObj.key === "notification") {
              permissionObj.isAllowed =
                permissionResponse[6].status === "fulfilled" &&
                permissionResponse[6].value === 1;
            }
            return permissionObj;
          }
        );
        const isResolved: boolean =
          permissions.filter(
            (data: AppPermission) => data.required && !data.isAllowed
          ).length === 0;
        let snackBarMessage: SnackBarMessageData = {
          open: false,
          message: "",
          key: `${new Date()}`,
        };
        if (!isOnline) {
          snackBarMessage.open = true;
          snackBarMessage.message = "YouAreNotConnectedToInternet";
          snackBarMessage.severity = "error";
        }
        this.setState((prevState: State, props: Props) => {
          return {
            permissions,
            resolved: isResolved,
            loading: false,
            online: isOnline,
            snackBarMessageData: isOnline
              ? undefined
              : {
                  ...snackBarMessage,
                },
          };
        });
      })
      .catch((error: any) => {
        this.setState({
          resolved: true,
          loading: false,
          error: error.message ?? "Something went wrong!",
        });
      });
  }

  render() {
    const { classes } = this.props;
    const {
      resolved,
      loading,
      permissions,
      online,
      snackBarMessageData,
    } = this.state;
    if (!resolved) {
      let child: ReactNode = null;
      if (loading) {
        child = <LinearProgress className={classes.loading} />;
      } else {
        child = <div>dad</div>;
      }
      return (
        <React.Fragment>
          <CssBaseline />
          <Container className={classes.container}>{child}</Container>
        </React.Fragment>
      );
    }
    return (
      <Provider
        value={{
          permissions,
          online,
          isOnline: this.isOnline,
          requestForPermission: this.requestForPermission,
        }}
      >
        {this.props.children}
        {snackBarMessageData && (
          <SnackBarMessage
            key={snackBarMessageData.key}
            open={snackBarMessageData.open}
            onClose={this.handleSnackBarMessageClose}
            severity={snackBarMessageData.severity}
            message={<Trans id={snackBarMessageData.message} />}
          />
        )}
      </Provider>
    );
  }
}

export default withStyles(styles)(PermissionProvider);
