import {
  createStyles,
  Snackbar,
  Theme,
  withStyles,
  WithStyles,
} from "@material-ui/core";
import MuiAlert, { AlertProps, Color } from "@material-ui/lab/Alert";
import React from "react";

const styles = (theme: Theme) => createStyles({});

export type SnackBarMessageProps = {
  open: boolean;
  onClose: () => void;
  severity?: Color;
  message: React.ReactNode;
};

const Alert = (props: AlertProps) => {
  return <MuiAlert {...props} />;
};

const SnackBarMessage: React.FC<
  SnackBarMessageProps & WithStyles<typeof styles>
> = ({ classes, open, onClose, severity, message }) => {
  return (
    <Snackbar open={open} onClose={onClose} autoHideDuration={5000}>
      <Alert onClose={onClose} severity={severity}>
        {message}
      </Alert>
    </Snackbar>
  );
};

export default withStyles(styles)(SnackBarMessage);
