import React, { ReactNode } from "react";
import * as Sentry from "@sentry/react";
import firebase from "firebase/app";
import "firebase/analytics";
import "firebase/performance";
import AppError from "src/utils/errors/AppError";

export type CrashlyticsContext = {
  reportError?: (appError: AppError) => void;
  firebaseAnalytics?: firebase.analytics.Analytics;
  firebasePerformance?: firebase.performance.Performance;
};

const defaultContext = {};

const Context = React.createContext<CrashlyticsContext>(defaultContext);

const { Consumer, Provider } = Context;

export { Consumer, Context };

type State = {
  error?: any;
};

type Props = {
  children: ReactNode;
  firebaseApp: firebase.app.App;
};

class CrashlyticsProvider extends React.Component<Props, State> {
  private firebaseAnalytics: firebase.analytics.Analytics;
  private firebasePerformance?: firebase.performance.Performance;

  constructor(props: Props) {
    super(props);
    this.state = {
      error: undefined,
    };
    const { firebaseApp } = props;
    this.firebaseAnalytics = firebaseApp.analytics();
    if (process.env.NODE_ENV !== "production") {
      this.firebasePerformance = firebaseApp.performance();
    }
  }

  reportError = (appError: AppError) => {
    this.firebaseAnalytics.logEvent("exception", { ...appError });
  };

  onError = (error: Error, componentStack: string, eventId: string) => {
    this.reportError(new AppError(error.message, "crashlytics error occurred"));
  };

  render() {
    return (
      <Provider
        value={{
          reportError: this.reportError,
          firebaseAnalytics: this.firebaseAnalytics,
          firebasePerformance: this.firebasePerformance,
        }}
      >
        <Sentry.ErrorBoundary
          fallback={<div>{"error"}</div>}
          onError={this.onError}
        >
          {this.props.children}
        </Sentry.ErrorBoundary>
      </Provider>
    );
  }
}

export default CrashlyticsProvider;
